@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.ProcutCard{  
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 285px;
    box-shadow:0 0 10px 0 rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    margin-top: 20px;
    
}

.productCard-text-color-size-btn{
    z-index: 1;
    background: white;
    border-radius: 15px;
    padding-left: 30px;
    padding-right: 30px;
}
.div__img__card{
    height: 280px;
    width: 285px;
}

.image__card{
    border-radius: 20px;
    width: 285px;
    -webkit-transform: scaleX(-1) ;
            transform: scaleX(-1) ;
}
.text-block__nameProduct-price{
    margin-top: 30px;
    font-family: 'Nunito', sans-serif;
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    font-size: 14px;
    
}

.text-block__description{
    margin-top: 0;
    font-family: 'Nunito', sans-serif;
    text-align: justify;
    font-size: 14px;
}
.color-product{
    height: 60px;
}

.color__label{
    display: flex;
    margin-bottom: 5px;
    font-size: 15px;
    font-weight: bold;
}

.color-product-select{
    height: 50px;
    display: flex;
    flex-direction: row;
    grid-gap: 9px;
    gap: 9px;
}

.color__black{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    background: black;
}

.color__darkviolet{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    background: rgb(40, 7, 146);
}

.color__yellow{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    background: rgb(226, 226, 10);
}

.color__gray{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    background: gray;
}
.size-div{
    margin-bottom: 27px;
}

.size__product {
    display: flex;
    margin-bottom: 5px;
    font-size: 15px;
    font-weight: bold;
}

.size__product__select{
    display: flex;
    flex-direction: row;
    grid-gap: 10px;
    gap: 10px;
    font-size: 12px;
    line-height: 20px;
    margin-bottom: 20px;
}

.size__small{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    background: black;
    color: white;
}

.size__medium{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    background: rgb(185, 185, 185);
    color: black;
}

.size__large{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    background: rgb(185, 185, 185);
    color: black;
}

.btnCart{
    width: 180px;
    height: 40px;
    background: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 10px;
    margin-bottom: 28px;
}
